<!---->
<template>
  <div id="terminalManege">
    <el-tabs v-model="activeTab" @tab-click="handleClick">
      <!-- tab -->
      <el-tab-pane label="" name="allView">
        <!--  -->
        <el-row>
          <el-col :span="5" v-for="(item,index) in allDataArr" :key="index" style="margin:0px 10px">
            <el-card class="box-card allCard" shadow="hover" :body-style="{ padding:'15px'}">
              <div slot="header" class="clearfix">
                <span>{{item.title}}</span>
                <span class="pull-right" :class="item.color">{{item.timeTitle}}</span>
              </div>
              <h1>{{item.count}}</h1>
              <small>{{item.smallTitle}}</small>
              <div class="stat-percent" :class="item.color">
                {{item.percent}}
                <i :class="item.icon"></i>
              </div>
            </el-card>
          </el-col>
        </el-row>
        <!--  -->
        <el-row>
          <el-col :span="20" :offset="2">
            <div id="mapChart" style="height:400px"></div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <!-- tab -->
      <el-tab-pane label="" name="detailView">
        <el-row>
          <el-form :inline="true" :model="searchItem" ref="searchItem">
            <el-form-item label="ID" prop="id">
              <el-input v-model="searchItem.id" placeholder="ID"></el-input>
            </el-form-item>
            <el-form-item prop="version" label="">
              <el-select v-model="searchItem.version" :placeholder="versionArr.title">
                <el-option
                  v-for="item in versionArr.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="ascription" label="">
              <el-select v-model="searchItem.ascription" :placeholder="ascriptionArr.title">
                <el-option
                  v-for="item in ascriptionArr.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search(1)"></el-button>
              <el-button type="default" @click="reset('searchItem')"></el-button>
            </el-form-item>
          </el-form>
        </el-row>
        <el-row>
          <el-col :span="22">
            <div>
              <el-table
                :data="tableData"
                border
                size="medium"
                class="terminalTable"
                @selection-change="getRowDatas"
              >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column type="index" width="50" label=""></el-table-column>
                <el-table-column prop="id" width="150" label="ID"></el-table-column>
                <el-table-column prop="version" label=""></el-table-column>
                <el-table-column prop="ability" width="150" label=""></el-table-column>
                <el-table-column prop="container" label=""></el-table-column>
                <el-table-column prop="status" label="">
                   <template slot-scope="scope">
                     <span v-if="scope.row.status==='1'" style="color:#67c23a"></span>
                     <span v-if="scope.row.status==='0'" style="color:red"></span>
                     <span v-if="scope.row.status!=='0'&&scope.row.status!=='1'" style="color:orange"></span>
                     </template>
                </el-table-column>
                <el-table-column prop="attribution" width="150" label=""></el-table-column>
                <el-table-column prop="options" label="" width="200">
                  <template slot-scope="scope">
                    <el-button @click="editRow(scope.row)" type="text" size="medium"></el-button>
                    <el-button @click="delRow(scope.row)" type="text" size="medium"></el-button>
                    <el-button @click="updateRow(scope.row)" type="text" size="medium"></el-button>
                    <el-button @click="pairRow(scope.row)" type="text" size="medium"></el-button>  
                  </template>
                </el-table-column>
              </el-table>
              <el-row style="margin:20px 0px">
                <el-button-group>
                  <el-button
                    type="success"
                    round
                    size="small"
                    icon="el-icon-refresh"
                    @click="search(1,10)"
                  ></el-button>
                  <el-button
                    type="primary"
                    round
                    size="small"
                    icon="el-icon-plus"
                    @click="add('dialogForm')"
                  ></el-button>
                  <el-button
                    type="warning"
                    round
                    size="small"
                    icon="el-icon-upload2"
                    @click="importRows()"
                  ></el-button>
                </el-button-group>
                <el-pagination
                  background
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-sizes="[5, 10,15]"
                  :page-size="tableLimit"
                  layout="total, sizes, prev, pager, next"
                  :total="tableSize"
                  style="float: right"
                ></el-pagination>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>

    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
      <el-form :model="dialogForm" :rules="dialogRules" ref="dialogForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-col :span="15">
            <el-input v-model="dialogForm.name" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="version">
          <el-col :span="15">
            <el-select v-model="dialogForm.version" placeholder="" style="width:100%">
              <el-option
                v-for="item in versionArr.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="ability">
          <el-col :span="15">
            <el-input v-model="dialogForm.ability" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="container">
          <el-col :span="15">
            <el-input v-model="dialogForm.container" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="ascription">
          <el-col :span="15">
            <el-select v-model="dialogForm.ascription" placeholder="" style="width:100%">
              <el-option
                v-for="item in ascriptionArr.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="dialogForm.status" label="0"></el-radio>
          <el-radio v-model="dialogForm.status" label="1"></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('dialogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import "../../node_modules/echarts/map/js/china.js";
const geoCoordMap = {};
export default {
  name: "terminalManege",
  data() {
    return {
      activeTab: "allView",
      tableSize: 0,
      tableLimit: 10,
      selectedRow: [],
      searchItem: {
        id: "",
        version: "",
        ascription: ""
      },
      versionArr: {
        title: "",
        options: [
          {
            value: "1",
            label: "1.0"
          },
          {
            value: "2",
            label: "1.1"
          },
          {
            value: "3",
            label: "1.2"
          }
        ]
      },
      ascriptionArr: {
        title: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          },
          {
            value: "3",
            label: ""
          }
        ]
      },
      allDataArr: [
        {
          title: "",
          timeTitle: "",
          smallTitle: "",
          count: "236,495",
          percent: "98%",
          icon: "fa fa-long-arrow-up",
          color: "blue"
        },
        {
          title: "",
          timeTitle: "",
          smallTitle: "",
          count: "235,964",
          percent: "98%",
          icon: "fa fa-long-arrow-up",
          color: "green"
        },
        {
          title: "",
          timeTitle: "",
          smallTitle: "8",
          count: "6,956",
          percent: "98%",
          icon: "fa fa-long-arrow-up",
          color: "greenAll"
        },
        {
          title: "",
          timeTitle: "",
          smallTitle: "",
          count: "3,235",
          percent: "98%",
          icon: "fa fa-long-arrow-up",
          color: "orange"
        }
      ],
      mapData: [
        { name: "", value: 102 },
        { name: "", value: 81 },
        { name: "", value: 47 },
        { name: "", value: 67 },
        { name: "", value: 123 },
        { name: "", value: 92 },
        { name: "", value: 114 },
        { name: "", value: 109 },
        { name: "", value: 116 },
        { name: "", value: 91 },
        { name: "", value: 119 },
        { name: "", value: 137 },
        { name: "", value: 116 },
        { name: "", value: 114 },
        { name: "", value: 125 },
        { name: "", value: 62 },
        { name: "", value: 83 },
        { name: "", value: 9 },
        { name: "", value: 80 },
        { name: "", value: 56 },
        { name: "", value: 10 },
        { name: "", value: 18 },
        { name: "", value: 180 },
        { name: "", value: 123 },
        { name: "", value: 59 },
        { name: "", value: 14 }
      ],

      tableData: [
        {
          id: "MathCAD",
          version: "1.0",
          ability: "test1",
          container: "test1",
          status: "1",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "2.0",
          ability: "test2",
          container: "test2",
          status: "0",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "1.2",
          ability: "test3",
          container: "test3",
          status: "",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "1.4",
          ability: "test4",
          container: "test4",
          status: "1",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "1.0",
          ability: "test1",
          container: "test1",
          status: "0",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "2.0",
          ability: "test2",
          container: "test2",
          status: "",
          attribution: ""
        },
        {
          id: "MathCAD",
          version: "1.2",
          ability: "test3",
          container: "test3",
          status: "",
          attribution: ""
        }
      ],
      dialogTitle: "",
      dialogFormVisible: false,
      dialogForm: {
        name: "",
        version: "",
        ability: "",
        container: "",
        status: "0",
        ascription: ""
      },
      dialogRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        version: [
          { required: true, message: "", trigger: "change" }
        ],
        ability: [
          { required: true, message: "", trigger: "change" }
        ],
        container: [
          { required: true, message: "", trigger: "change" }
        ],
        ascription: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  mounted() {
    this.tableSize = this.tableData.length;
    this.drawMap();
  },
  methods: {
    search(page) {
      // this.$axios.post('',{
      //   id:this.searchItem.id,
      //   version:this.searchItem.version,
      //   ascription:this.searchItem.ascription,
      //   limit:this.tableLimit,
      //   page:page,
      //   sort:'asc'
      // }).then((res)=>{
      //   this.tableSize=res.data.length;
      //   this.tableData=res.data;
      // }).catch((err)=>{
      //   console.log(err);
      // });
    },
    reset(formName) {
      this.$refs[formName].resetFields();
    },
    getRowDatas(row) {
      this.selectedRow = row;
      console.info(row);
    },
    //
    getMapData(){

    },
    drawMap() {
      let myChart = this.$echarts.init(document.getElementById("mapChart"));
      myChart.showLoading();
      let mapFeatures = this.$echarts.getMap("china").geoJson.features;
      myChart.hideLoading();
      mapFeatures.forEach(function(v) {
        // 
        let name = v.properties.name;
        // 
        geoCoordMap[name] = v.properties.cp;
      });
      let option = {
        tooltip: {
          padding: 0,
          enterable: true,
          transitionDuration: 1,
          textStyle: {
            color: "#000",
            decoration: "none"
          },
          alwaysShowContent:true,
          formatter: function(params) {
            var tipHtml = '';
            if(params.value!==undefined&&!Number.isNaN(params.value)){
            tipHtml = '<div style="width:200px;height:80px;background:rgba(22,80,158,0.8);border:1px solid rgba(7,166,255,0.7)">'
            +'<div style="width:90%;height:30px;line-height:30px;border-bottom:2px solid rgba(7,166,255,0.7);padding-left:15px">'
            +'<i style="display:inline-block;width:8px;height:8px;background:#16d6ff;border-radius:40px;"></i>'
            +'<span style="margin-left:10px;color:#fff;font-size:14px;">'+params.name+'</span>'
            +'</div>'
            +'<div style="padding:15px 5px">'
            +'<p style="color:#fff;font-size:12px;">'+'<i style="display:inline-block;width:10px;height:10px;background:#16d6ff;border-radius:40px;margin:0 8px"></i>'
            +''+'<span style="color:#11ee7d;margin:0 6px;">'+params.value+'</span>'+''+'</p>'
            +'</div>'+'</div>';
            }else{
            tipHtml = '<div style="width:200px;height:80px;background:rgba(22,80,158,0.8);border:1px solid rgba(7,166,255,0.7)">'
            +'<div style="width:90%;height:30px;line-height:30px;border-bottom:2px solid rgba(7,166,255,0.7);padding-left:15px">'
            +'<i style="display:inline-block;width:8px;height:8px;background:#16d6ff;border-radius:40px;"></i>'
            +'<span style="margin-left:10px;color:#fff;font-size:14px;">'+params.name+'</span>'
            +'</div>'
            +'<div style="padding:15px 5px">'
            +'<p style="color:#fff;font-size:12px;">'+'<i style="display:inline-block;width:10px;height:10px;background:#16d6ff;border-radius:40px;margin:0 8px"></i>'
            +''+'<span style="color:#11ee7d;margin:0 6px;">---</span>'+''+'</p>'
            +'</div>'+'</div>';
            }
            return tipHtml;
          }
        },
        visualMap: {
          show: true,
          min: 0,
          max: 200,
          left: "10%",
          top: "bottom",
          calculable: true,
          seriesIndex: [1],
          inRange: {
            color: ["#04387b", "#467bc0"] // 
          }
        },
        geo: {
          show: true,
          map: "china",
          zoom:1.2,
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: "#023677",
              borderColor: "#1180c7"
            },
            emphasis: {
              areaColor: "#4499d0"
            }
          }
        },
        series: [
          {
            name: "",
            type: "scatter",
            coordinateSystem: "geo",
            data: this.convertData(this.mapData),
            symbolSize: function(val) {
              return val[2] / 10;
            },
            label: {
              normal: {
                formatter: "{b}",
                position: "right",
                show: true
              },
              emphasis: {
                show: true
              }
            },
            itemStyle: {
              normal: {
                color: "#fff"
              }
            }
          },
          {
            type: "map",
            map: "china",
            geoIndex: 0,
            aspectScale: 0.85, //
            showLegendSymbol: false, // legend
            label: {
              normal: {
                show: true
              },
              emphasis: {
                show: false,
                textStyle: {
                  color: "#fff"
                }
              }
            },
            roam: true,
            itemStyle: {
              normal: {
                areaColor: "#031525",
                borderColor: "#3B5077"
              },
              emphasis: {
                areaColor: "#2B91B7"
              }
            },
            animation: false,
            data: this.mapData
          },
          {
            name: "",
            type: "scatter",
            coordinateSystem: "geo",
            zlevel: 6
          },
          {
            name: "Top 5",
            type: "effectScatter",
            coordinateSystem: "geo",
            data: this.convertData(
              this.mapData
                .sort(function(a, b) {
                  return b.value - a.value;
                })
                .slice(0, 5)
            ),
            symbolSize: function(val) {
              return val[2] / 10;
            },
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            hoverAnimation: true,
            label: {
              normal: {
                formatter: "{b}",
                position: "left",
                show: false
              }
            },
            itemStyle: {
              normal: {
                color: "yellow",
                shadowBlur: 10,
                shadowColor: "yellow"
              }
            },
            zlevel: 1
          }
        ]
      };
      myChart.setOption(option);
      myChart.resize();  
    },
    convertData(data) {
      let res = [];
      for (let i = 0; i < data.length; i++) {
        if (geoCoordMap[data[i].name] !== undefined) {
          let geoCoord = geoCoordMap[data[i].name];
          if (geoCoord) {
            res.push({
              name: data[i].name,
              value: geoCoord.concat(data[i].value)
            });
          }
        }
      }
      return res;
    },
    //
    add(formName) {
      this.dialogTitle = "";
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
      this.dialogFormVisible = true;
    },
    //
    editRow(row) {
      //
        // this.$axios.post('',{
        //   id:this.selectedRow[0].id,'
        // }).then((res)=>{
        //   this.search(page);
        // }).catch((err)=>{
        //   console.log(err);
        // });
        this.dialogTitle = "";
        this.dialogForm = row;
        this.dialogFormVisible = true;
    },
    //
    delRow(row) {
      this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            // this.$axios.post('',{
            //   id:row.id,'
            // }).then((res)=>{
            //   this.search(page);
            // }).catch((err)=>{
            //   console.log(err);
            // });
            this.$message({
              type: "success",
              message: "!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
    },
    //
    updateRow(row){

    },
    //
    pairRow(row){

    },
    //
    importRows(){

    },
    ///
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // this.$axios.post('',{
          //   dialogForm,
          // }).then((res)=>{
          this.dialogFormVisible = false;
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs[formName].resetFields();
          //   this.search(page);
          // }).catch((err)=>{
          //   console.log(err);
          // });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //tab
    handleClick(){},
    handleSizeChange(val) {
      this.tableLimit = val;
      this.search(1);
    },
    handleCurrentChange(val) {
      this.search(val);
    }
  }
};
</script>

<style scoped>
.terminalTable {
  max-height: 500px;
  overflow: auto;
}
.pull-right {
  color: #ffffff;
  font-size: 10px;
  font-weight: 600;
  padding: 3px 8px;
  text-shadow: none;
}
.allCard {
  border: 1px solid #eee !important;
}
.allCard h1 {
  font-size: 36px;
  margin-bottom: 5px;
}
.el-card__header {
  padding: 10px 20px !important;
}
.stat-percent {
  color: #1c84c6;
  font-weight: 600;
  float: right;
}
.el-card__header .blue {
  background-color: #1c84c6;
}
.el-card__header .green {
  background-color: #23c6c8;
}
.el-card__header .greenAll {
  background-color: #1ab394;
}
.el-card__header .orange {
  background-color: rgba(240, 155, 119, 1);
}
.el-card__body .blue {
  color: #1c84c6;
}
.el-card__body .green {
  color: #23c6c8;
}
.el-card__body .greenAll {
  color: #1ab394;
}
.el-card__body .orange {
  color: rgba(240, 155, 119, 1);
}
.tooltipDiv{
  
}
</style>



